<template>
  <div class="close-trade">
    <slot />
    <el-dialog
      class="close-trade__close-order-dialog"
      :visible.sync="curDialogVisible"
      append-to-body
      width="400px"
      top="25vh"
      @closed="resetCom"
    >
      <div class="close-trade__close-order-dialog__body">
        <div class="close-trade__close-order-dialog__body__tips">
          <i class="el-icon-warning" />
          <span>请您在于用户达成一致的前提下，使用关闭交易这个功能哟！</span>
        </div>
        <div class="close-trade__close-order-dialog__content">
          <div class="close-trade__close-order-dialog__content__close-txt">
            请选择关闭交易的理由
          </div>
          <el-select
            v-model="closeReason"
            class="close-trade__close-order-dialog__content__select"
            placeholder="请选择关闭交易的理由"
            size="small"
          >
            <el-option
              v-for="{ key, value } in closeReasons"
              :key="key"
              :value="key"
              :label="value"
            />
          </el-select>
        </div>
      </div>
      <span slot="footer" class="close-trade__close-order-dialog__footer">
        <el-button type="primary" size="small" @click="handleClosePendingOrder"
          >关闭交易</el-button
        >
        <el-button
          class="close-trade__close-order-dialog__cancel"
          size="small"
          @click="curDialogVisible = false"
          >取消并返回</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { closeTrade as closeTradeApi } from '@/api/order'
import { statusUpdateMessage } from '@/utils/statusUpdateMessage'

export default {
  model: {
    prop: 'dialogVisible',
    event: 'change'
  },
  props: {
    id: {
      type: String,
      required: true
    },
    dialogVisible: {
      type: Boolean,
      default: false,
      required: true
    }
  },
  data() {
    return {
      curDialogVisible: false,
      closeReason: '',
      closeReasons: [
        {
          key: '1',
          value: '用户不想买了'
        },
        {
          key: '2',
          value: '其它'
        }
      ]
    }
  },
  watch: {
    dialogVisible(value) {
      if (value !== this.curDialogVisible) this.curDialogVisible = value
    },
    curDialogVisible(value) {
      this.$emit('change', value)
    }
  },
  methods: {
    handleCloseTrade(id, closeReaonKey) {
      return new Promise(resolve => {
        closeTradeApi(id, {
          close_reason: closeReaonKey
        }).then(res => {
          statusUpdateMessage(res)
          resolve()
        })
      })
    },
    async handleClosePendingOrder() {
      if (!this.closeReason) {
        this.$message({
          message: '请选择关闭交易的理由',
          type: 'warning'
        })
        return
      }
      await this.handleCloseTrade(this.id, this.closeReason).then(async () => {
        this.curDialogVisible = false
        this.$emit('closeTradeAfter')
      })
    },
    resetCom() {
      this.closeReason = ''
    }
  }
}
</script>

<style lang="scss">
.close-trade {
  display: flex;
  justify-content: center;
  &__close-order-dialog {
    .el-dialog {
      &__header {
        display: none;
      }
      &__body {
        padding: 20px 30px 30px 30px;
      }
      &__footer {
        text-align: center;
      }
    }
    .el-button + &__cancel {
      margin-left: 20px;
      color: #999;
    }
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.close-trade {
  &__close-order-dialog {
    &__body {
      @include ff-regular;
      &__tips {
        display: flex;
        justify-content: flex-start;
        margin-left: -26px;
        width: 100%;
        line-height: 20px;
        font-size: 12px;
        color: #f0717b;
        margin-bottom: 16px;
        transform: scale(0.84);
        i {
          margin-right: 8px;
          height: 20px;
          line-height: 20px;
        }
        span {
          min-width: 380px;
          flex-grow: 1;
          word-break: break-all;
          word-wrap: break-word;
        }
      }
    }
    &__content {
      &__select {
        display: block;
      }
      &__close-txt {
        margin-right: -36px;
        color: #{$textDeepBlock};
        margin-bottom: 14px;
      }
    }
  }
}
</style>
