<template>
  <div class="server-info">
    <div class="baby__title">{{ titleByType(curServerInfo).title }}信息</div>
    <div class="baby__content">
      <div class="baby__content__item">
        {{ titleByType(curServerInfo).name }}
      </div>
      <div v-if="type === 'erke'" class="baby__content__item">
        性别： {{ curServerInfo.family_member.gender | gender }}
      </div>
      <div v-if="type === 'erke'" class="baby__content__item">
        出生日期：
        {{
          new Date(curServerInfo.family_member.birthday)
            | formatTime('{y}-{m}-{d}')
        }}
      </div>
      <div
        class="baby__content__item"
        v-if="curServerInfo.family_member.gestational_weeks"
      >
        孕周： {{ curServerInfo.family_member.gestational_weeks }}周{{
          curServerInfo.family_member.gestational_days
        }}天
      </div>
      <div class="baby__content__item">
        {{ titleByType(curServerInfo).age }}
      </div>
      <div class="baby__content__item">
        {{ titleByType(curServerInfo).profession }}
      </div>
      <div class="baby__content__item">
        {{ titleByType(curServerInfo).curStage }}
      </div>
    </div>

    <card :title="'用户信息'">
      <div slot="title">
        <el-button
          type="text"
          explain
          @click="seeProductDetail"
          v-if="curServerInfo.fd_product"
          >套餐信息</el-button
        >
        <el-button type="text" explain @click="handleSeeDetail"
          >套餐使用详情</el-button
        >
      </div>
      <div class="corps-info">
        <div>
          基础团队：
          {{ curServerInfo.fd_group.name }}
          （
          <span v-if="type !== 'chanke'">
            医生：{{ curServerInfo.doctor && curServerInfo.doctor.name }}，
          </span>
          {{ titleByType(curServerInfo).hmName }}：{{
            curServerInfo.hm && curServerInfo.hm.name
          }}
          ）
        </div>
        <div v-if="curServerInfo.dietician">
          营养师团队：营养师团队（营养师：{{ curServerInfo.dietician.name }}）
        </div>
      </div>
    </card>
  </div>
</template>

<script>
import Card from '@/components/Card'

import { curStageTxt } from '@/filters'

export default {
  components: { Card },
  props: {
    curServerInfo: {
      type: Object,
      default: () => {
        return {
          family_member: {},
          fd_group: {},
          dietician: {},
          hm: {},
          doctor: {}
        }
      }
    }
  },
  filters: {
    gender(v) {
      return v === 1 ? '男' : '女'
    }
  },
  data() {
    return {
      type: ''
    }
  },
  created() {
    this.type = this.curServerInfo.family_member.type
  },
  methods: {
    curStageTxt,
    titleByType(curServerInfo) {
      const type = curServerInfo.family_member.type
      let obj = {
        title: '',
        name: '',
        age: '',
        curStage: ''
      }
      switch (type) {
        case 'chanke':
          obj = {
            title: '成员',
            name: '姓名：' + curServerInfo.family_member.name,
            profession:
              '职业：' + curServerInfo.family_member.chanke.profession,
            age:
              '年龄：' +
              curServerInfo.family_member.age_of_month_value.year +
              '岁',
            curStage:
              '当前阶段：' +
              this.curStageTxt(curServerInfo.family_member.chanke.cur_stage),
            hmName: '产科医生'
          }
          break
        case 'erke':
          obj = {
            title: '宝宝',
            name: '乳名：' + curServerInfo.family_member.name,
            profession: '',
            age: '',
            curStage: '',
            hmName: '健康管理师'
          }
          break
      }
      return obj
    },
    getServerDate(activeDate, endDate) {
      if (!activeDate) return '未知'
      else
        return (
          this.formatTime(activeDate, '{y}-{m}-{d}') +
          ' 到 ' +
          this.formatTime(endDate, '{y}-{m}-{d}')
        )
    },
    handleSeeDetail() {
      this.$message({
        type: 'info',
        message: '预留按钮，文字链接'
      })
    },
    seeProductDetail() {
      this.$router.push({
        name: 'FdServiceEdit',
        params: {
          id: this.curServerInfo.fd_product,
          type: 'detail'
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';
.server-info {
  .card {
    padding-right: 0;
    padding-left: 0;
    border-width: 0;
    border-radius: 0;
  }
  .card + .card {
    border-top-style: dashed;
    border-top-width: 1px;
  }
  .el-table {
    &__header {
      th {
        background-color: #f5f5f5;
      }
    }
    &__row:hover {
      background-color: none;
    }
  }
  &__detail-btn {
    margin-left: 30px;
    font-size: 16px;
  }
}
</style>
<style lang="scss" scoped>
@import '~@/styles/variables.scss';
.server-info {
  padding-bottom: 30px;
}
.baby__title {
  margin-top: 30px;
  line-height: 40px;
  font-size: 16px;
}
.baby__content {
  display: flex;
  color: #576277;
  &__item {
    margin-right: 50px;
  }
}
.corps-info {
  line-height: 30px;
}
</style>
