export function tradeStatusFilter(value) {
  switch (value) {
    case 'PENDING':
      return '待付款'
    case 'SUCCESS':
      return '交易成功'
    case 'CLOSED':
      return '交易关闭'
    default:
      if (value) {
        throw new Error(`无法识别的订单状态: ${value}`)
      } else {
        return ''
      }
  }
}
