export function orderTypeFilter(value) {
  switch (value) {
    case 'FdServiceType':
      return '家庭医生'
    case 'InquiryServiceType':
      return '问诊'
    default:
      throw new Error(`无法识别的订单类型: ${value}`)
  }
}

export function afterSaleStatusFilter(value) {
  switch (value) {
    case 'apply_pending':
      return '请处理退款申请'
    case 'refund_refused':
      return '退款已被拒绝'
    case 'refund_process':
      return '退款中'
    case 'refund_success':
      return '退款成功'
    default:
      throw new Error(`无法识别的售后状态: ${value}`)
  }
}
