<template>
  <div class="record-list">
    <div
      v-for="(record, index) in recordArr"
      :key="index"
      class="record-list__item"
      :class="{ 'record-list__item--last': index === recordArr.length - 1 }"
    >
      <message :msg="record" />
    </div>
  </div>
</template>

<script>
import Message from './message'

export default {
  components: { Message },
  props: {
    recordArr: {
      type: Array,
      default: () => []
    }
  }
}
</script>

<style lang="scss">
.record-list {
  &__item {
    .body {
      border-bottom: 1px dashed #ebeef5;
    }
  }
  &__item--last {
    .body {
      border-bottom-color: #fff;
    }
  }
}
</style>
