<template>
  <div class="order-info">
    <card :title="'用户信息'">
      <el-form :inline="true" :model="userInfo" class="demo-form-inline">
        <el-form-item label="用户ID:">{{ userInfo.userId }}</el-form-item>
        <el-form-item label="微信昵称:">{{ userInfo.nickName }}</el-form-item>
        <el-form-item label="所在地区:">{{ userInfo.address }}</el-form-item>
        <el-form-item label="手机型号:">{{ userInfo.phoneModel }}</el-form-item>
        <el-form-item label="联系电话:">{{
          userInfo.mobileNumber
        }}</el-form-item>
      </el-form>
    </card>
    <card :title="'订单信息'">
      <el-form :inline="true" :model="orderInfo" class="demo-form-inline">
        <el-form-item label="订单号:">{{ orderInfo.orderId }}</el-form-item>
        <el-form-item label="微信交易号:">{{
          orderInfo.paymentNumber
        }}</el-form-item>
        <el-form-item label="支付方式:">{{
          orderInfo.paymentType
        }}</el-form-item>
        <el-form-item label="创建时间:">{{
          orderInfo.createdDate | formatTime
        }}</el-form-item>
        <el-form-item v-if="tradeStatus !== 'CLOSED'" label="支付时间:">{{
          orderInfo.payedDate
        }}</el-form-item>
        <el-form-item v-if="tradeStatus === 'CLOSED'" label="交易关闭时间:">{{
          orderInfo.closedDate
        }}</el-form-item>
      </el-form>
    </card>
    <DetailTable :tableData="orderDetailData" />
    <div class="margin-top-20 margin-bottom-10" v-if="orderInfo.fdStatus">
      当前服务状态：{{ orderInfo.fdStatus }}
    </div>
    <div v-if="orderInfo.fdStatus === '服务中'">
      激活时间：{{ contractInfo.started_at | formatTime }}
    </div>
    <el-button
      type="primary"
      @click="activeService"
      v-if="orderInfo.fdStatus === '待激活'"
      size="small"
      >立即激活</el-button
    >
    <OrderRemark
      :title="'订单备注'"
      :orderOrRefundId="orderInfo._id"
      :logicType="'Order'"
      :orderId="orderInfo._id"
    ></OrderRemark>
  </div>
</template>

<script>
import Card from '@/components/Card'
import DetailTable from './DetailTable'
import { orderTypeFilter } from '../filters'
import { fenToYuan, RMBSymbol } from '@/filters'
import OrderRemark from './OrderRemark'
const defaultUserInfo = {
  id: '',
  wxNickName: '',
  address: '',
  phoneModel: '',
  mobileNumber: ''
}

const defaultOrderInfo = {
  orderId: '',
  paymentNumber: '',
  paymentType: '',
  createdDate: new Date(),
  payedDate: new Date()
}

const defaultOrderTableInfo = {
  orderName: '',
  orderType: 'FdServiceType',
  order_action: '',
  orderStatus: { value: '' },
  transactionStatus: '',
  actualUnitPrice: 0,
  count: 0
}
const defaultContractInfo = {
  status: '',
  _id: ''
}
export default {
  components: { Card, OrderRemark, DetailTable },
  filters: {
    // contractStatus(status) {
    //   // 当前服务状态：待激活、待生效、服务中、已过期、已退款、已关闭
    //   if (status === 'pending_active') {
    //     return '待激活'
    //   }
    //   if (status === 'pending_service') {
    //     return '待生效'
    //   }
    //   if (status === 'in_service') {
    //     return '服务中'
    //   }
    //   if (status === 'out_service') {
    //     return '已过期'
    //   }
    //   if (status === 'refund') {
    //     return '已退款'
    //   }
    //   if (status === 'close') {
    //     return '已关闭'
    //   }
    //   return '无'
    // }
  },
  props: {
    curOrderInfo: {
      type: Object,
      default: () => {}
    },
    tradeStatus: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      orderDetailData: [],
      orderRemark: 'order-remark',
      userInfo: Object.assign({}, defaultUserInfo),
      orderInfo: Object.assign({}, defaultOrderInfo),
      orderTableInfo: Object.assign({}, defaultOrderTableInfo),
      contractInfo: Object.assign({}, defaultContractInfo)
    }
  },
  watch: {
    curOrderInfo: {
      handler: function(value) {
        if (value.user && value.order) {
          this.getOrderDetail(value)
        }
      },
      deep: true
    }
  },
  methods: {
    fenToYuan,
    RMBSymbol,
    activeService() {
      this.$emit('activeService')
    },
    getOrderDetail({ user = {}, order = {}, contract = {} }) {
      this.userInfo = Object.assign(defaultUserInfo, {
        userId: user.user_id,
        nickName: user.nick_name,
        address: user.area,
        phoneModel: user.phone_code,
        mobileNumber: user.mobile
      })

      this.orderInfo = Object.assign(defaultOrderInfo, {
        _id: order._id || this.$route.params.id,
        orderId: order.order_id,
        paymentNumber: order.out_trade_no,
        paymentType: order.payment_type,
        createdDate: order.created_at,
        closedDate: this.formatTime(order.closed_at), //交易关闭日期
        payedDate: order.payed_at ? this.formatTime(order.payed_at) : '',
        cost: order.cost, // 实付款
        fdStatus: order.fd_status,
        sumPrice: order.cost_price ? order.cost_price : '' // 订单总价
      })
      // console.log('contract====', contract)
      // console.log('order====', order)
      this.orderTableInfo = Object.assign(defaultOrderTableInfo, {
        orderName: order.order_name,
        orderType: order.order_type ? order.order_type : '',
        orderTypeName: orderTypeFilter(order.order_type),
        order_action: order.order_action, //签约类型
        orderStatus: order.status.value,
        actualUnitPrice: RMBSymbol(fenToYuan(order.percost)), //单价
        coupon: order.coupon_name
          ? `${order.coupon_name}(${RMBSymbol(order.coupon_value)})`
          : '无',
        fdRight: order.quanyi_name
          ? `${order.quanyi_name}(${RMBSymbol(order.quanyi_value)})`
          : '无',
        cost: RMBSymbol(fenToYuan(order.cost)), //实付款
        count: order.count
      })
      this.orderDetailData = [this.orderTableInfo]
      if (contract._id) {
        this.contractInfo = Object.assign(defaultContractInfo, {
          _id: contract._id,
          status: contract.status,
          started_at: contract.started_at
        })
      } else {
        this.contractInfo = {
          status: '',
          _id: ''
        }
      }
      console.log('orderInfo id==', this.orderInfo._id)
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';
.order-info {
  .card {
    padding-right: 0;
    padding-left: 0;
    border-width: 0;
    border-radius: 0;
  }
  .card + .card {
    border-top-style: dashed;
    border-top-width: 1px;
  }
  .el-form-item {
    margin-right: 70px;
    min-width: 190px;
    margin-bottom: 0;
    &__label,
    &__content {
      height: 20px;
      line-height: 20px;
    }
    &__label {
      @include ff-medium;

      color: #{$textLightBlock};
    }
  }
  .el-table {
    &__header {
      th {
        background-color: #f5f5f5;
      }
    }
    &__row:hover {
      background-color: none;
    }
  }
}
</style>
<style lang="scss" scoped>
@import '~@/styles/variables.scss';
.order-info {
  &__table {
    margin-bottom: 20px;
  }
}
</style>
