<template>
  <div>
    <card class="rbd-consult-record">
      <div slot="title" class="rbd-consult-record__title">
        <span>{{ title }}</span>
        <el-button type="text" @click="editorIsShow = !editorIsShow"
          >添加备注</el-button
        >
      </div>
      <div class="rbd-consult-record__body">
        <el-collapse-transition>
          <div v-if="editorIsShow" class="rbd-consult-record__body__editor">
            <el-input
              v-model="edittingRecord.text"
              class="rbd-consult-record__body__editor__text"
              type="textarea"
              :rows="5"
              placeholder="请输入内容"
              maxlength="200"
              show-word-limit
            />
            <uploader-list
              v-model="edittingRecord.image"
              class="rbd-consult-record__body__editor__image"
              width="70px"
              height="70px"
              :max="3"
            />
            <el-button type="primary" size="small" @click="handleAddRemark"
              >提交</el-button
            >
          </div>
        </el-collapse-transition>
        <consult-record :record-arr="orderRecords" />
      </div>
    </card>
  </div>
</template>
<script>
import { addRemark, getRemark } from '@/api/order'
import { statusUpdateMessage } from '@/utils/statusUpdateMessage'
import UploaderList from '@/components/UploaderList'
import Card from '@/components/Card'
import ConsultRecord from '@/views/trade-manage/refund/components/ConsultRecord'
export default {
  data() {
    return {
      editorIsShow: false,
      edittingRecord: {
        text: '',
        image: []
      },
      orderRecords: []
    }
  },
  props: {
    updateRemark: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default: '退款状态及协商历史'
    },
    orderId: {
      type: String,
      default: ''
    },
    logicType: {
      type: String,
      default: ''
    },
    orderOrRefundId: {
      type: String,
      require: true,
      default: ''
    }
  },
  components: {
    Card,
    ConsultRecord,
    UploaderList
  },
  watch: {
    orderOrRefundId() {
      this.getRemarkList()
    },
    updateRemark(val) {
      console.log('updateRemark===', val)
      this.getRemarkList()
    }
  },
  created() {
    this.getRemarkList()
  },
  methods: {
    getRemarkList() {
      if (this.orderOrRefundId) {
        getRemark({ logic_id: this.orderOrRefundId }).then(res => {
          this.orderRecords = res.data
        })
      }
    },
    handleAddRemark() {
      const { text, image } = this.edittingRecord
      const data = []
      if (text) {
        data.push({
          type: 'text',
          content: text
        })
      }
      if (image.length > 0) {
        data.push({
          type: 'image',
          content: image
        })
      }
      if (data.length === 0) {
        this.$message.warning('添加的备注不能为空！')
      } else {
        if (!this.orderOrRefundId) {
          this.$message.warning('订单或退款id不能为空！')
          return
        }
        addRemark({
          logic_id: this.orderOrRefundId, // 订单日志为订单的_id , 退款日志为 退款的_id
          logic_type: this.logicType, // 'Order' : 'RefundOrder', // 订单为Order 退款为RefundOrder
          order: this.orderId,
          remark: data
        }).then(async res => {
          statusUpdateMessage(res)
          this.edittingRecord = {
            text: '',
            image: []
          }
          this.editorIsShow = false
          await this.getRemarkList()
        })
      }
    }
  }
}
</script>
<style lang="scss" scope>
.rbd-consult-record {
  flex-grow: 1;
  &__title {
    display: flex;
    justify-content: rows;
    .el-button {
      margin-left: 30px;
      padding-top: 4px;
      padding-bottom: 4px;
    }
  }
  &__body {
    &__editor {
      padding: 20px;
      background: #f5f7fa;
      margin-bottom: 10px;
      &__text {
        margin-bottom: 8px;
      }
      &__image {
        margin-bottom: 14px;
      }
    }
  }
}
</style>
