<template>
  <div class="server-info">
    <card :title="'用户信息'">
      <div slot="title">
        <span style="color:#409eff">服务信息</span>
        <span class="server-info__detail-btn" @click="handleSeeDetail"
          >详情</span
        >
      </div>
      <el-table
        class="server-info__table"
        :data="curServerInfo"
        style="width: 100%"
      >
        <el-table-column label="用户" align="center">
          <template slot-scope="scope">
            <div class="avatar">
              <el-avatar :src="scope.row.user.avatar" />
              <p>{{ scope.row.user.nickname }}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          label="服务科室"
          align="center"
          prop="section_office"
          :formatter="row => sectionOffice(row.section_office, '', true)"
        />
        <el-table-column
          label="接诊人"
          align="center"
          prop="doctor"
          :formatter="row => row.doctor.name"
        />
        <el-table-column align="center" label="提问时间" min-width="110">
          <template slot-scope="scope">
            {{ scope.row.active_at ? formatTime(scope.row.active_at) : '未知' }}
          </template>
        </el-table-column>
        <el-table-column
          label="问诊状态"
          align="center"
          prop="status"
          :formatter="row => singleAskStatus(row.status, '')"
        />
        <el-table-column
          label="首次提问等待时间"
          align="center"
          prop="active_at"
          :formatter="waitTime"
        />
      </el-table>
    </card>
  </div>
</template>

<script>
import Card from '@/components/Card'
import { sectionOffice, singleAskStatus } from '@/utils/dictionary'
export default {
  components: { Card },
  props: {
    curServerInfo: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    sectionOffice,
    singleAskStatus,
    handleSeeDetail() {
      this.$message({
        type: 'info',
        message: '预留按钮，文字链接'
      })
    },
    waitTime(row) {
      const { hour, minutes } = this.getHours(
        row.first_ask_at,
        row.first_reply_at || new Date()
      )
      return `${hour}小时${minutes}分钟`
    },
    getHours(start, end) {
      const startAt = new Date(start)
      startAt.setMinutes(0)
      startAt.setSeconds(0)
      startAt.setMilliseconds(0)
      const endAt = new Date(end)
      endAt.setMinutes(0)
      endAt.setSeconds(0)
      endAt.setMilliseconds(0)
      let hour = 0
      // eslint-disable-next-line no-unmodified-loop-condition
      while (startAt < endAt) {
        const startHour = startAt.getHours()
        if (startHour > 8 && startHour <= 23) {
          hour += 1
        }
        startAt.setTime(startAt.setHours(startAt.getHours() + 1))
      }
      const startTime = new Date(start)
      const endTime = new Date(end)
      const startHour = startTime.getHours()
      const endHour = endTime.getHours()
      let startMinutes = startTime.getMinutes()
      let startSeconds = startTime.getSeconds()
      let startMilliseconds = startTime.getMilliseconds()
      if (startHour <= 7) {
        startMinutes = 0
        startSeconds = 0
        startMilliseconds = 0
      }
      let endMinutes = endTime.getMinutes()
      let endSeconds = endTime.getSeconds()
      let endMilliseconds = endTime.getMilliseconds()
      if (endHour <= 7 && endHour > 0) {
        endMinutes = 0
        endSeconds = 0
        endMilliseconds = 0
      } else if (endHour === 0) {
        endMinutes = 59
        endSeconds = 59
        endMilliseconds = 0
      }
      let minutes = endMinutes - startMinutes
      let seconds = endSeconds - startSeconds
      let milliseconds = endMilliseconds - startMilliseconds
      if (milliseconds < 0) {
        seconds = seconds - 1
        milliseconds = 1000 + milliseconds
      }
      if (seconds < 0) {
        minutes = minutes - 1
        seconds = 60 + seconds
      }
      if (minutes < 0) {
        hour = hour - 1
        minutes = 60 + minutes
      }
      return { hour, minutes, seconds, milliseconds }
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';
.server-info {
  .card {
    padding-right: 0;
    padding-left: 0;
    border-width: 0;
    border-radius: 0;
  }
  .card + .card {
    border-top-style: dashed;
    border-top-width: 1px;
  }
  .el-table {
    &__header {
      th {
        background-color: #f5f5f5;
      }
    }
    &__row:hover {
      background-color: none;
    }
  }
  &__detail-btn {
    margin-left: 30px;
    font-size: 16px;
    color: #17233d;
  }
}
</style>
<style lang="scss" scoped>
@import '~@/styles/variables.scss';
.server-info {
  padding-bottom: 30px;
}
</style>
