<template>
  <div class="msg-wrapper">
    <div class="msg">
      <div class="avatar">
        <img :src="msg.user.avatar" />
      </div>
      <div class="body">
        <div class="info">
          <div class="name">{{ msg.user.name }}</div>
          <div class="date fontsize-12">{{ formatTime(msg.created_at) }}</div>
        </div>
        <div class="contents">
          <div
            v-for="(item, index) in msg.remark"
            :key="`${Number(new Date()) + index}`"
            class="contents__item"
          >
            <div
              v-if="item.type === 'text'"
              class="contents__item--text"
              v-html="getText(item.content)"
            />
            <div v-if="item.type === 'image'">
              <uploader-list
                :uploadList="getImageArr"
                style="display:inine-block;"
                width="70px"
                height="70px"
                disabled
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import UploaderList from '@/components/UploaderList'

const defaultMsg = {
  user: { name: '', avatar: '' },
  remark: [],
  sendDate: ''
}

export default {
  components: { UploaderList },
  props: {
    msg: {
      type: Object,
      default: () => defaultMsg
    }
  },
  data() {
    return {
      userRemark: null,
      read: false
    }
  },
  computed: {
    getImageArr() {
      let remarkArr = this.msg.remark
      let imageArr = []
      if (remarkArr.length) {
        for (let i = 0; i < remarkArr.length; i++) {
          if (remarkArr[i].type === 'image') {
            for (let j = 0; j < remarkArr[i].content.length; j++) {
              imageArr.push(remarkArr[i].content[j])
            }
          }
        }
        return imageArr
      } else {
        return ''
      }
    }
  },
  methods: {
    getText(str) {
      return str.replace('\n', '<br>')
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.msg {
  display: flex;
  align-items: flex-start;
  padding: 10px 0;
  .avatar {
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
  .body {
    padding-bottom: 16px;
    padding-left: 10px;
    font-size: 14px;
    flex: 1;
    .info {
      display: flex;
      align-items: center;
      line-height: 20px;
      .name {
        @include ff-medium;

        color: #{$textDeepBlock};
      }
      .date {
        margin-left: 20px;
        color: #999;
      }
    }
    .contents {
      padding-top: 4px;
      &__item {
        &--text {
          line-height: 22px;
        }
        &--image {
          padding-top: 6px;
        }
      }
    }
  }
}
</style>
