<template>
  <div v-loading="loading">
    <MyTable
      size="mini"
      :stripe="false"
      :border="true"
      :multiple="false"
      :outlineBorder="false"
      :operation="operation"
      :column="column"
      :data="tableData"
      :currentPage="currentPage"
      :pageSize="pageSize"
      :total="total"
    >
    </MyTable>
  </div>
</template>
<script>
import { orderDetailTableColumn } from '../../tableColumn.js'
import MyTable from '@/components/MyTable'
export default {
  data() {
    // 表格操作配置
    var operation = {
      nowPage: 'orderDetailTable',
      show: false
    }
    return {
      userId: '',
      column: [],
      loading: false,
      formItem: [],
      formData: {},
      pageSize: 10,
      currentPage: 1,
      total: 0,
      operation: operation // 操作按钮
    }
  },
  props: {
    tableData: {
      type: Array,
      default: () => []
    }
  },
  components: {
    MyTable
  },
  created() {
    this.fieldInit()
  },
  methods: {
    fieldInit() {
      // 获取字段
      var column = orderDetailTableColumn()
      column.forEach(item => {
        if (!!item.width && item.width != 'auto') {
          item.width = '300'
        }
      })
      this.column = column
    }
  }
}
</script>
